.form {
	&-row {
		position: relative;
		margin: 0 0 20px 0;
		padding: 0px;
		&:before {
			content: "";
			position: absolute;
			top: 15px;
			left: 11px;
			background: url(../images/sprite/spritesheet-form.png) no-repeat;
		}
		&__name:before {
			.stitches-sprite(-21px, -53px, 19px, 18px);
		}
		&__theme:before {
			.stitches-sprite(-71px, -1px, 18px, 18px);
		}
		&__email:before {
			.stitches-sprite(-1px, -53px, 18px, 15px);
		}
		&__text:before {
			.stitches-sprite(-42px, -53px, 18px, 18px);
		}
	}
	&__input, &__textarea {
		display: inline-block;
		vertical-align: top;
		width: 100%;
		border: 1px solid #d3d3d3;
		border-radius: 2px;
		color: #8b8b8b;
		font: 400 14px/50px @OpenSansRegular;
	}
	&__input {
		height: 50px;
		padding: 0 0 0 40px;
		box-shadow: 0 0 7px 0 rgba(2,2,2, .17) inset;
		background: none;
		&:focus {
			border: 1px solid #9dbff6;
		}
	}

	&__textarea {
		min-height: 200px;
		padding: 0 0 0 40px;
		resize: none; 
	}
	
	&-checkbox {
		position: relative;
		display: inline-block;
		vertical-align: top;
		&__input {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			opacity: 0;
			z-index: 0;
		}
		&__text {
			color: #000;
			font-size: 16px;
			line-height: 16px;
			&:before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				z-index: 1;
			}
		}
	}

	&__submit {
		display: inline-block;
		vertical-align: top;
		width: 150px;
		height: 50px;
		background: #3fc736;
		background: -moz-linear-gradient(top,  #3fc736 0%, #048733 100%);
		background: -webkit-linear-gradient(top,  #3fc736 0%,#048733 100%);
		background: linear-gradient(to bottom,  #3fc736 0%,#048733 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#3fc736', endColorstr='#048733',GradientType=0 );
		border: 1px solid #048733;
		border-radius: 2px;
		color: #fff;
		font: 600 18px/50px @OpenSansRegular;
		text-align: center;
		text-transform: uppercase;
		cursor: pointer;
		&:hover {
			background: #048733;
			background: -moz-linear-gradient(top,  #048733 0%, #3fc736 100%);
			background: -webkit-linear-gradient(top,  #048733 0%,#3fc736 100%);
			background: linear-gradient(to bottom,  #048733 0%,#3fc736 100%);
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#048733', endColorstr='#3fc736',GradientType=0 );
		}
		&:disabled {
			background: #dfdfdf;
			border: 1px solid #cdcdcd;
			color: #b6b6b6;
		}
	}

	&__textarea::-webkit-input-placeholder, &__input::-webkit-input-placeholder {
		color: #8b8b8b;
	}
	&__textarea::-moz-placeholder, &__input::-moz-placeholder {
		color: #8b8b8b;
	}
	&__textarea:-ms-input-placeholder, &__input:-ms-input-placeholder {
		color: #8b8b8b;
	}
	&__textarea:-moz-placeholder, &__input:-moz-placeholder {
		ccolor: #8b8b8b;
	}

}