.logo {
	max-width: 210px;
	&__elem {
		display: inline-block;
		vertical-align: top;
		color: #555;
		font-size: 38px;
		line-height: 38px;
		font-family: @PhantasmAll;
		text-decoration: none;
		&:hover {
			text-decoration: none;
		}
	}
	&__text {
		display: inline-block;
		vertical-align: top;
		width: 100%;
		margin: 10px 0 0;
		color: #545454;
		font-size: 15px;
		line-height: 15px;
	}
}