.w-content {
	max-width: 1300px;
	margin: 0 auto;
}

.info {
	background: #f5f5f5;
	min-height: 25px;
	line-height: 25px;
	.w-content {
		text-align: right;
	}
}

.header {
	background: #fbfbfb;
	.w-content {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		min-height: 90px;
	}
}

.main {
	flex: 1 0 auto;
}

.footer {
	.w-content {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		min-height: 78px;
		border-top: 1px solid #e4e4e4 ;
	}
	
}