.project-desc {
	display: flex;
	flex-direction: row;
	margin: 40px 0 80px;
	font-size: 14px;
	line-height: 24px;
	&__image {
		margin: 0 60px 0 0;
		img {
			border: 0;
			box-shadow: 0 0 15px 0 rgba(128,128,128, .5);
		}
	}
	&-info {
		&__target, &__date {
			display: inline-block;
			vertical-align: top;
			color: #000;
			font-weight: 600;
		}
		&__target {
			margin: 0 0 35px;
		}
		&__date {
			margin: 0 0 30px;
		}
		&__text {
		
		}
	}
}