.review-list {
	column-count: 3;
	column-gap: 3%;
	column-width: 30%;
	margin: 30px 0 0;
	&__item {
		display: inline-block;
		vertical-align: top;
		width: 100%;
		margin: 0 0 40px 0;
		padding: 20px;
		background: #fbfbfb;;
		border: 1px solid #d3d3d3;
	}
	&__item:nth-child(3n) {
		margin-right: 0;
	}
	&-author {
		position: relative;
		min-height: 80px;
		margin: 0 0 10px 0;
		padding: 0 0 0 100px;
		
		&__avatar {
			position: absolute;
			top: 0;
			left: 0;
			width: 80px;
			height: 80px;
			border: 0;
			
		}
		&__name, &__work {
			display: inline-block;
			vertical-align: top;
			width: 100%;
		}
		&__name {
			color: #000;
			font-weight: 600;
			font-size: 16px;
			line-height: 24px;
		}
		&__work {
			color: #7f7f7f;
			font-weight: 400;
			font-size: 13px;
			line-height: 18px;
		}
		
	}
	&-wrap {
		p {
			margin: 0 0 20px 0;
			color: #5b5b5b;
			font-size: 16px;
			line-height: 24px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}