.project {
	position: relative;
	margin: 0;
	color: #5b5b5b;
	text-align: center;

	&__icon, &__text {
		display: inline-block;
		vertical-align: top;
	}

	&__w-icon {
		display: flex;
		width: 265px;
		height: 200px;
		margin: 0 auto;
	}

	&__icon {
		border: 0;
		margin: auto;
	}

	&__text, &__elem {
		font-size: 14px;
		line-height: 20px;
	}

	&__text {
		width: 100%;
		color: #5b5b5b;
	}

	&__elem {
		color: #1b65a4;
		text-decoration: underline;
		&:hover {
			text-decoration: none;
		}
	}
	&__elem:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}
.project-wrap {
	font-size: 0;
	padding: 40px 0 0;
	.project {
		display: inline-block;
		vertical-align: top;
		width: 20.5%;
		margin-right: 6%;
		margin-bottom: 50px;
		&:nth-child(4n) {
			margin-right: 0;
		}
		&__w-icon {
			margin-bottom: 18px;
		}
	}
}