/*--FONTS--*/
@font-face {font-family:'Open Sans Regular';font-weight:400;font-style:normal;src:url('../fonts/OpenSans-Regular.ttf') format('truetype');}
@font-face {font-family:'Open Sans Semibold';font-weight:600;font-style:normal;src:url('../fonts/OpenSans-Semibold.ttf') format('truetype');}
@font-face {font-family:'PhantasmAll';font-weight:400;font-style:normal;src:url('../fonts/PhantasmAllCaps.ttf') format('truetype');}

@OpenSansRegular: 'Open Sans Regular', sans-serif;
@OpenSansSemibold: 'Open Sans Semibold', sans-serif;
@PhantasmAll: PhantasmAll,  @OpenSansRegular, sans-serif;

/*--/fonts--*/