.fancybox-skin {
	background: #fff;
	border-radius: 0;
}
.modal {
	width: 600px;
	padding: 25px 20px 30px;
	&-title {
		display: inline-block;
		vertical-align: top;
		width: 100%;
		margin: 0 0 25px 0;
		color: #6a6d84;
		font-weight: 700;
		font-size: 24px;
		line-height: 24px;
		text-align: center;
		text-transform: uppercase;
	}
}