.info-elem {
	display: inline-block;
	vertical-align: middle;
	margin: 0 28px 0 0;
	color: #5b5b5b;
	font-size: 11px;
	line-height: 16px;
	text-decoration: none;
	&:last-child {
		margin-right: 0;
	}
	&:nth-child(2) {
		margin-right: 25px;
	}
	&:before {
		content: "";
		display: inline-block;
		vertical-align: middle;
		margin: 0 4px 0 0;
		background: url(../images/sprite/spritesheet-info.png) no-repeat;
	}
	&:first-child:before {
		.stitches-sprite(-1px, -17px, 14px, 14px);
	}
	&:nth-child(2):before {
		.stitches-sprite(-21px, -1px, 12px, 14px);
	}
	&:last-child:before {
		.stitches-sprite(-1px, -1px, 18px, 13px);
	}
}