.contact {
	max-width: 215px;
	text-align: right;
	&__phone, &__email {
		display: inline-block;
		vertical-align: top;
	}
	&__phone {
		width: 100%;
		color: #545454;
		font-weight: 600;
		font-size: 24px;
		line-height: 24px;
	}
	&__email {
		margin: 5px 0 0;
		color: #1b65a4;
		font-size: 15px;
		line-height: 15px;
		&:hover {
			text-decoration: none;
		}
		&:before {
			content: "";
			display: inline-block;
			vertical-align: middle;
			width: 14px;
			height: 12px;
			margin: 0 6px 0 0;
			background: url(../images/icon/mail-icon.png) no-repeat 0 0;
		}
	}
}