.skills-section {
	margin:90px 0 0;
	.gui-title-block {
		margin-bottom: 56px;
	}
}
.skills {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	padding: 0 0 0 10px;
	&__item {
		position: relative;
		margin: 0 0 18px 0;
		padding: 0 10px 0 55px;
	}
	&__item:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		background: url(../images/sprite/spritesheet-skills.png) no-repeat;
	}
	&__item:nth-child(2) {
		padding-left: 60px;
	}
	&__item:nth-child(5) {
		padding-left: 64px;
	}
	&__item:nth-child(3n+1) {
		flex: 430px 1;
	}
	&__item:nth-child(3n+2) {
		flex: 446px 1;
	}
	&__item:nth-child(3n+3) {
		flex: 376px 1;
	}

	&__item:first-child:before {
		.stitches-sprite(-1px, -1px, 32px, 42px);
	}
	&__item:nth-child(2):before {
		.stitches-sprite(-35px, -1px, 47px, 42px);
	}
	&__item:nth-child(3):before {
		.stitches-sprite(-1px, -45px, 33px, 42px);
	}
	&__item:nth-child(4):before {
		.stitches-sprite(-36px, -45px, 32px, 42px);
	}
	&__item:nth-child(5):before {
		.stitches-sprite(-84px, -1px, 53px, 42px);
	}
	&__item:nth-child(6):before {
		.stitches-sprite(-70px, -45px, 42px, 42px);
	}

	&__title, &__desc {
		display: inline-block;
		vertical-align: top;
	}
	&__title {
		margin: 0 0 10px 0;
		color: #7c83af;
		font-weight: 600;
		font-size: 16px;
		line-height: 16px;
	}
	&__desc {
		font-size: 14px;
		line-height: 24px;
	}
}