.technology-desc {
	margin: 26px 0 64px;
}

.technology-column {
	display: inline-block;
	vertical-align: top;
	width: 424px;
	border-right: 1px solid #c4c6d5;
	&:first-child {
		padding-right: 25px;
	}
	&:nth-child(2) {
		width: 447px;
		padding-right: 0;
		padding-left: 0;
	}
	&:last-child {
		padding-left: 25px;
		border-right: 0;
	}
}

.technology-list {
	display: flex;
	justify-content: space-between;
	align-items: stretch;
	font-size: 0;
	&-wrap {
		margin: 35px 0 0;
	}
	&__wrap-elem {
		display: inline-block;
		vertical-align: middle;
		width: 50%;
		margin: 0 0 25px 0;
		text-align: center;
	}
	&__elem {
		display: inline-block;
		border: 0;
		vertical-align: middle;
	}
}

.technology-column:nth-child(2) {
	.technology-list__wrap-elem {
		margin-bottom: 50px;
	}
	.technology-list-wrap {
		margin-bottom: -50px;
	}
}