.gui-h1 {
	position: relative;
	margin: 45px 0 0;
	color: #6a6d84;
	font-weight: 700;
	font-size: 24px;
	line-height: 24px;
	text-align: center;
	&:before {
		content: "";
		position: absolute;
		top: 50%;
		left: 0;
		z-index: 0;
		width: 100%;
		height: 1px;
		background: #c4c6d5;
	}
	span {
		position: relative;
		z-index: 1;
		display: inline-block;
		vertical-align: top;
		padding: 0 20px;
		background: #fff;
		text-transform: uppercase;
	}
}

.gui-h2 {
	color: #6a6d84;
	font-weight: 700;
	font-size: 18px;
	line-height: 18px;
	text-align: center;
}

.gui-title-block {
	display: inline-block;
	vertical-align: top;
	width: 100%;
	color: #6a6d84;
	font-weight: 700;
	font-size: 24px;
	line-height: 24px;
	text-transform: uppercase;
}

.caption {
	& img {
		margin: 0 0 20px 0;
		border: 0;
	}
	&__text {
		display: inline-block;
		vertical-align: top;
		width: 100%;
		font-size: 14px;
		& span {
			color: #006eb5;
		}
	}
}

.about-short {
	.caption {
		position: absolute;
		bottom: 0;
		left: 0;
	}
}

.framework {
	span {
		color: #232323;
	}
	span:first-child {
		color: #006eb5;
	}
}