.about-list {
	margin-top: 33px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	&__item, &__number, &__desc {
		display: inline-block;
		vertical-align: top;
	}
	
	&__item {
		max-width: 280px;
		text-align: center;
	}
	&__number {
		margin: 0 0 7px 0;
		font-weight: 600;
		color: #df463f;
		font-size: 42px;
		line-height: 46px;
	}
	&__desc {
		color: #3d3d3d;
		font-weight: 400;
		font-size: 15px;
		line-height: 15px;
	}
}

.about-short {
	position: relative;
	padding: 0 0 65px 0;
	.gui-title-block {
		margin-bottom: 15px;
	}
	p {
		margin: 0 0 25px 0;
		font-size: 14px;
		line-height: 24px;
	}
	
}
