.slider {
	position: relative;
	width: 100%;
	min-height: 275px;
	margin: 54px 0 45px;
	padding: 0 40px;
	.project {
		width: 100%;
	}
}

.owl-theme .owl-controls .owl-buttons div {
	width: 18px;
	height: 34px;
	background: url(../images/sprite/spritesheet-slider.png) no-repeat;
}
.owl-theme .owl-controls .owl-buttons {
	.owl-prev {
		position: absolute;
		top: 26%;
		left: 0;
		.stitches-sprite(-21px, -1px, 19px, 34px);
	}
	.owl-next {
		position: absolute;
		top: 26%;
		right: 0;
		.stitches-sprite(-1px, -1px, 18px, 34px);
	}
}
